body {
  background-color: #f5f7fe;
}

.dialog-box {
  width: 400px;
  height: 700px;
  padding: 0px 35px 35px 35px;
}

.dialog-sub-box textarea {
  width: 325px;
  height: 100px;
  border-radius: 5px;
}

.home-top-bar {
  display: flex;
  align-items: center;
  gap: 40rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: #f5f7fe;
  padding: 0rem 1rem 0rem 12rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  height: 100px;
}

.logo-section {
  font-size: 2.6rem;
  font-weight: bold;
  cursor: pointer;
}

.home-top-bar-section.button-section>#navbar-rev {
  
  border: 3px solid gray;
  color: gray;
  border-radius: 10px;
  font-weight: bold;
  margin-left: 50px;

}

/* #0BB980 */

.home-main-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  /* margin: 5rem 2rem 5rem 10rem; */
  padding: 30px;
}

#navbar-search-box {
  width: 25rem;
  margin-right: 14px;
  height: 2.7em;
  border-radius: 24px;
  border: 1px solid gray;
}

#navbar-search-box:focus {
  border-color: gray;
}

.main-lgn {
  display: flex;
  flex-direction: column;
  padding: 5px;
  align-items: center;
}

.login-text {
  font-size: 2rem;
  color: black;
  font-weight: bold;
  height: 30px;
  height: 60px;
}

.input-1-box {
  display: flex;
  flex-direction: column;
  margin: 8px 0px 8px 0px;
}

.input-1-box input {
  height: 35px;
  width: 270px;
  border: 1px solid rgb(178, 176, 176);
  border-radius: 5px;
  padding-left: 10px;
  outline: none;
  transition: 0.5s;
}

.input-2-box input {
  height: 35px;
  width: 270px;
  border: 1px solid rgb(178, 176, 176);
  border-radius: 5px;
  padding-left: 10px;
  outline: none;
  transition: 0.5s;
}

input:focus {
  border: 2px solid #0bb980;
}

.input-2-box label {
  color: rgb(178, 176, 176);
}

.input-1-box label {
  color: rgb(178, 176, 176);
}

.input-2-box {
  display: flex;
  flex-direction: column;
  margin: 8px 0px 8px 0px;
}

.lgn-btn {
  width: 200px;
  background: rgb(32, 55, 226);
  background: linear-gradient(344deg,
      rgba(32, 55, 226, 1) 0%,
      rgba(11, 185, 128, 1) 72%);
  border-radius: 5px;
  height: 30px;
  margin-top: 20px;
  text-align: center;
}

.lgn-btn Button {
  color: white;
  width: 200px;
}

.top {
  width: 70vw;
  min-height: 300px;
  /* background-color: #0bb980; */
  display: flex;
  justify-content: space-between;
}

.left {
  width: 45%;
  min-height: 300px;
  /* border: 2px solid black; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.right {
  width: 45%;
  min-height: 300px;
  /* border: 2px solid black;  */
}

.tag {
  color: #0bb980;
  font-weight: bold;
}

.tag2 {
  font-size: 2.5rem;
  font-weight: bold;
  margin: 20px 0px 20px 0px;
}

.numbers {
  color: rgb(166, 164, 164);
  font-size: 15px;
}

.bottom {
  /* width: 70vw; */
  display: flex;
  flex-direction: column;
}

.companies {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 10px;
}

.companies img {
  height: 85px;
  width: 150px;
  /* border: 1px solid rgb(186, 183, 183); */
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

@media (max-width: 768px) {
  .home-top-bar {
    width: 100%;
    padding: 0;
    margin: 0;
  }

  .logo-section {
    z-index: 99;
    position: absolute;
  }

  .home-top-bar-section {
    font-size: 14vw;
    width: 10%;
    margin-left: 33px;
  }

  .button-section {
    font-size: 10px;
    width: 100%;
    margin-left: 60px;
  }

  .menu-icon {
    margin-right: 0px;
  }

  .dialog-box {
    width: 97%;
    padding-right: 0px;
    padding-left: 10px
  }

  .home-main-section {
    width: 100%;
    padding: 0;
  }

  .top {
    flex-direction: column;
    width: 100%;
  }

  .left {
    padding-left: 38px;
    width: 85%;
  }

  .tag2 {
    font-size: 2.3rem;
  }

  .right {
    width: 100%;
  }

  .companies {
    width: 400px;
    flex-wrap: wrap;
    margin-top: 50px;
  }

  .bottom {
    width: 100vw;
  }

  .home-top-bar-section.button-section>#navbar-rev{
    width: 90px;
    font-size: 13px;
  }

}

@media (max-width: 400px) and (min-width: 300px) {
  .companies {
    width: 347px;
  }

  .companies img {
    width: 190px;
    height: 90px;
  }

  .dialog-box {
    width: 90%;
    padding-right: 0px;
    padding-left: 10px
  }
}